import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument } from '@angular/fire/firestore';
import { UserApi} from '../models/users/usersapi';
import { Observable } from 'rxjs';
import { StorageService } from './storage/storage.service'; 
import { WareHouse } from '../models/wareHouse';



@Injectable()
export class WareHouseService {

  //Establecimientos: AngularFirestoreCollection<AddEstablishment>;
  waresHouse: AngularFirestoreDocument<WareHouse>
  //addestablishment: Observable<AddEstablishment>

  AlmacenWebCollection: AngularFirestoreCollection<WareHouse>;
  wareHouse: Observable<WareHouse[]>;

  userDetail: UserApi;
  navbarCartCount = 0;
  navbarFavProdCount = 0;
  
  constructor(
    private fb: AngularFirestore,
    private storageService: StorageService
	//	private authService: AuthService,
  ) {

  }

  getWareHouse() {
	//this.products = this.db.list('products');
  //this.Establecimientos = this.db.collection('Establecimientos');

  //return this.firestore.collection('policies').snapshotChanges();
  //return this.Establecimientos;
  return this.fb.collection('AlmacenWeb', ref => ref.orderBy('id')).valueChanges();
  //return this.fb.collection('Establecimientos').snapshotChanges();
  
}

postWareHouse(data: WareHouse) {
  //const newId = this.fb.createId();
  //alert("myID:: "+newId)
  
  //this.storageService.setSession("mykey", newId);

  return this.fb.collection('AlmacenWeb').doc( this.storageService.getSession("mykey")).set(data);
}

updateWareHouse(value){
  //value.nameToSearch = value.name.toLowerCase();
  var update =  this.fb.collection('AlmacenWeb').doc(this.storageService.getSession("$key"));
  return update.update({
    id:  value.id,
    nombre: value.nombre,    
    //code: value.code,

  })
  .then(function() {
    console.log("Document successfully updated!");
})
.catch(function(error) {
    // The document probably doesn't exist.
    console.error("Error updating document: ", error);
});
}


deleteWareHouse(myid: string){
    this.fb.collection("AlmacenWeb").doc(myid).delete().then(function() {
    console.log("Document successfully deleted!");
    }).catch(function(error) {
      console.error("Error removing document: ", error);
      });

}

}


