import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument } from '@angular/fire/firestore';
import { UserApi} from '../../shared/models/users/usersapi';
import { Observable } from 'rxjs';

import { StorageService } from './storage/storage.service'; 
import { Sale } from '../models/sale';


@Injectable()
export class SaleService {

  //Establecimientos: AngularFirestoreCollection<AddEstablishment>;
  sales: AngularFirestoreDocument<Sale>
  //addestablishment: Observable<AddEstablishment>

  VentasWebCollection: AngularFirestoreCollection<Sale>;
  sale: Observable<Sale[]>;

  userDetail: UserApi;
  navbarCartCount = 0;
  navbarFavProdCount = 0;
  
  constructor(
    private fb: AngularFirestore,
    private storageService: StorageService
	//	private authService: AuthService,
  ) {

  }

  getSale() {
	//this.products = this.db.list('products');
  //this.Establecimientos = this.db.collection('Establecimientos');

  //return this.firestore.collection('policies').snapshotChanges();
  //return this.Establecimientos;
  return this.fb.collection('VentasWeb', ref => ref.orderBy('id')).valueChanges();
  //return this..collection('Establecimientos').snapshotChanges();
  
}

postSale(data: Sale) {
  //const newId = this.fb.createId();
  //alert("myID:: "+newId)
  
  //this.storageService.setSession("mykey", newId);

  return this.fb.collection('VentasWeb').doc( this.storageService.getSession("mykey")).set(data);
}

updateSale(value){
  //value.nameToSearch = value.name.toLowerCase();
  var update =  this.fb.collection('VentasWeb').doc(this.storageService.getSession("$key"));
  return update.update({
    //id:  value.id,
    producto: value.producto,    
    cantidad: value.cantidad,
    ubicacion: value.ubicacion,
    costo: value.costo,
    placa: value.placa,
    formaPago: value.formaPago,
    total: value.total,
    imagen: value.imagen,
  })
  .then(function() {
    console.log("Document successfully updated!");
})
.catch(function(error) {
    // The document probably doesn't exist.
    console.error("Error updating document: ", error);
});
}


deleteSale(myid: string){
    this.fb.collection("VentasWeb").doc(myid).delete().then(function() {
    console.log("Document successfully deleted!");
    }).catch(function(error) {
      console.error("Error removing document: ", error);
      });

}

}


export class FavouriteMarketing {
	VentasWeb: Sale;
	productId: string;
	userId: string;
}