import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument } from '@angular/fire/firestore';
import { PriceList } from '../models/priceList';
import { UserApi} from '../models/users/usersapi';
import { Observable } from 'rxjs';

import { StorageService } from './storage/storage.service'; 


@Injectable()
export class PriceListService {

  listPrecio: AngularFirestoreDocument<PriceList>

  ListaPrecioWeb: AngularFirestoreCollection<PriceList>;
  priceList: Observable<PriceList[]>;


  userDetail: UserApi;
  navbarCartCount = 0;
  navbarFavProdCount = 0;
  
  constructor(
    private fb: AngularFirestore,
    private storageService: StorageService
	//	private authService: AuthService,
  ) {}

  getPriceList() {
	//this.products = this.db.list('products');
  //this.ListaPrecioWeb = this.fb.collection('ListaPrecioWeb');
  //return this.db.collection('products');
  //return this.firestore.collection('policies').snapshotChanges();
  return this.fb.collection('ListaPrecioWeb', ref => ref.orderBy('id')).valueChanges();
}

  postPriceList(data: PriceList) {
  //this.products.push(data);
  // return this.firestore.collection('policies').add(policy);
  return this.fb.collection('ListaPrecioWeb').doc( this.storageService.getSession("mykey")).set(data);
}

updatePriceList(value){
  //value.nameToSearch = value.name.toLowerCase();
  var update =  this.fb.collection('ListaPrecioWeb').doc(this.storageService.getSession("$key"));
  return update.update({
    precio:  value.precio,
    producto: value.producto,    
  })
  .then(function() {
    console.log("Document successfully updated!");
})
.catch(function(error) {
    // The document probably doesn't exist.
    console.error("Error updating document: ", error);
});
}


deletePriceList(myid: string){
    this.fb.collection("ListaPrecioWeb").doc(myid).delete().then(function() {
    console.log("Document successfully deleted!");
    }).catch(function(error) {
      console.error("Error removing document: ", error);
      });

}



}

export class FavouriteMarketing {
	ListaPrecioWeb: PriceList;
	productId: string;
	userId: string;
}