import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument } from '@angular/fire/firestore';
import { AddEstablishment } from '../models/addEstablishment';
import { UserApi} from '../../shared/models/users/usersapi';
import { Observable } from 'rxjs';

import { StorageService } from './storage/storage.service'; 


@Injectable()
export class AddEstablishmentService {

  //Establecimientos: AngularFirestoreCollection<AddEstablishment>;
  establishment: AngularFirestoreDocument<AddEstablishment>
  //addestablishment: Observable<AddEstablishment>

  EstablecimientosCollection: AngularFirestoreCollection<AddEstablishment>;
  addestablishment: Observable<AddEstablishment[]>;

  userDetail: UserApi;
  navbarCartCount = 0;
  navbarFavProdCount = 0;
  
  constructor(
    private fb: AngularFirestore,
    private storageService: StorageService
	//	private authService: AuthService,
  ) {

  }

  getEstablishment() {
	//this.products = this.db.list('products');
  //this.Establecimientos = this.db.collection('Establecimientos');

  //return this.firestore.collection('policies').snapshotChanges();
  //return this.Establecimientos;
  return this.fb.collection('Establecimientos', ref => ref.orderBy('id')).valueChanges();
  //return this..collection('Establecimientos').snapshotChanges();
  
}

postEstablishment(data: AddEstablishment) {
  //const newId = this.fb.createId();
  //alert("myID:: "+newId)
  
  //this.storageService.setSession("mykey", newId);

  return this.fb.collection('Establecimientos').doc( this.storageService.getSession("mykey")).set(data);
}

updateEstablishment(value){
  //value.nameToSearch = value.name.toLowerCase();
  var update =  this.fb.collection('Establecimientos').doc(this.storageService.getSession("$key"));
  return update.update({
    id:  value.id,
    nombre: value.nombre,    
    tipo: value.tipo,
    direccion: value.direccion,
    rfc: value.rfc,
    razonSocial: value.razonSocial,
    telefono: value.telefono,
    email: value.email,
    referencia: value.referencia,
  })
  .then(function() {
    console.log("Document successfully updated!");
})
.catch(function(error) {
    // The document probably doesn't exist.
    console.error("Error updating document: ", error);
});
}


deleteEstablish(myid: string){
    this.fb.collection("Establecimientos").doc(myid).delete().then(function() {
    console.log("Document successfully deleted!");
    }).catch(function(error) {
      console.error("Error removing document: ", error);
      });

}

}


export class FavouriteMarketing {
	Establecimientos: AddEstablishment;
	productId: string;
	userId: string;
}