import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument } from '@angular/fire/firestore';
import { Product } from '../models/product';
import { UserApi} from '../models/users/usersapi';
import { Observable } from 'rxjs';

import { StorageService } from './storage/storage.service'; 


@Injectable()
export class ProductService {

  //Establecimientos: AngularFirestoreCollection<AddEstablishment>;
  productoWeb: AngularFirestoreDocument<Product>
  //addestablihment: Observable<AddEstablishment>

  ProductCollection: AngularFirestoreCollection<Product>;
  clients: Observable<Product[]>;

  userDetail: UserApi;
  navbarCartCount = 0;
  navbarFavProdCount = 0;
 
  
  constructor(
    private fb: AngularFirestore,
    private storageService: StorageService
	//	private authService: AuthService,
  ) {}

  getProduct() {
	//this.products = this.db.list('products');
  //this.ClientWeb = this.db.collection('ClientWeb');
  
  //return this.db.collection('products');
  //return this.firestore.collection('policies').snapshotChanges();
  return this.fb.collection('productoWeb', ref => ref.orderBy('id')).valueChanges();
  //return this.fb.collection('Establecimientos').snapshotChanges();

}

  postProduct(data: Product) {
  //this.products.push(data);
  // return this.firestore.collection('policies').add(policy);

  //this.storageService.setSession("mykey", newId);

  return this.fb.collection('productoWeb').doc( this.storageService.getSession("mykey")).set(data);
}

updateProduct(value){
  //value.nameToSearch = value.name.toLowerCase();
  var update =  this.fb.collection('productoWeb').doc(this.storageService.getSession("$key"));
  return update.update({
    costo:  value.costo,
    producto: value.producto,    
    tipo: value.tipo,
    
  })
  .then(function() {
    console.log("Document successfully updated!");
})
.catch(function(error) {
    // The document probably doesn't exist.
    console.error("Error updating document: ", error);
});
}

  
deleteProduct(myid: string){
    this.fb.collection("productoWeb").doc(myid).delete().then(function() {
    console.log("Document successfully deleted!");
    }).catch(function(error) {
      console.error("Error removing document: ", error);
      });

}



}

